body {
  box-sizing: border-box;
  --blue-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.b7336e49.ttf");
}

.wrapper {
  justify-content: space-between;
  width: 100%;
  margin: 0 50px;
  display: flex;
}

.navigation {
  display: block;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  display: flex;
}

.nav_list {
  flex-direction: row;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  color: #000;
  letter-spacing: normal;
  justify-content: center;
  align-self: center;
  height: 60px;
  font-weight: 100;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav_item:nth-child(n+2) {
  margin-left: 20px;
}

.navigation_logo {
  float: left;
  justify-content: center;
  align-self: center;
  height: 40px;
  display: block;
}

.nav_link:hover {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 36.6px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.is-active {
  color: var(--blue-color);
}
